import {debounce} from '@/common/utils.js';
export default {
	data() {
		return {
			flag: true,
			verify: false,
			sha: false,
			selected: {},
			serverList: [],
			numb: [],
			id: '',
			lens: 0,
			scrollTop: 0,
			old: {
				scrollTop: 0,
			},
		};
	},
	onLoad() {
	    this.selected = {};	
	},
	onShow() {
		var _this = this;
		_this.selected = {};
		uni.request({
			url: this.url + 'usermachine/transferlist/',
			method: 'GET',
			header: {
				Authorization: 'JWT' + ' ' + this.global_.token
			},
			success(res) {
				console.log(res)
				_this.serverList = res.data.data[1];
				_this.numb = res.data.data[0];
				
				let lens = 0;
				for (let i = 0; i < res.data.data[1].length; i++) {
					let item = res.data.data[1][i];
					lens += item.lens;
				}
				_this.lens = lens;
				if (_this.numb == 0) {
					_this.verify = true;
				} else {
					_this.verify = false;
				}
				if (_this.serverList.length != 0) {
					_this.flag = false;
				}else{
					_this.flag = true;
				}

			}
		});
	},
	methods: {
		backFront() {
			uni.navigateBack({
				delta:1
			})
		},
		scroll(e) {
			this.old.scrollTop = e.detail.scrollTop
		},
		moveHandle: function(e) {
			e.preventDefault();
			e.stopPropagation();
		},
		prompt: function() {
			this.sha = true;
		},
		validation: function() {
			uni.request({
				url: this.url + 'usermachine/machinestatus/',
				method: 'POST',
				header: {
					Authorization: 'JWT' + ' ' + this.global_.token
				},
				success(res) {
					if (res.statusCode == 200) {
						uni.showToast({
							title: '已验证'
						});
					}
				}
			});
			this.sha = false;
			this.verify = false;
		},
		handleSelectRadio(item) {
			if (item.id === this.selected.id) {
				this.selected = {};
			} else {
				this.selected = item;
			}
		},
		linkToTransfer: debounce(
			function(type) {
				var that = this;
				if (!this.selected.id) {
					uni.showToast({
						title: '请选中服务器',
						icon: 'none'
					});
					return false;
				}
				uni.request({
					url: this.url + 'usermachine/transfer/',
					method: 'GET',
					header: {
						Authorization: 'JWT' + ' ' + this.global_.token
					},
					success(res) {
						console.log(res)
						if (res.statusCode == 200) {
							uni.navigateTo({
								url: '../server/server?type=' + type + '&info=' + JSON.stringify(that.selected)
							});
						}
						if (res.statusCode == 400) {
							uni.showToast({
								title: '用户实名认证未通过或未设置资金密码',
								icon: 'none'
							});
						}
						if (res.statusCode == 204) {
							uni.showToast({
								title: '有服务器未验收',
								icon: 'none'
							});
						}
					}
				});
			},
			500,
			true
		),
		btn: function(type) {
	        this.linkToTransfer(type);
		},
		linkToTransfer1: debounce(
			function() {
				uni.navigateTo({
					url: '../record/record'
				});
			},
			500,
			true
		),
		jilu: function() {
			this.linkToTransfer1();
		},
		
	}
};
